.nav {
    display: flex;
    justify-content: flex-end;
    position: relative;
    left: -80px;
    margin-bottom: 100px;
}

.list {
    display: flex;
}

.link {
    display: block;
    position: relative;
    padding: 10px 20px;
    text-transform: uppercase;
    text-decoration: none;
    color: var(--primary-color);
    text-shadow: var(--primary-text-shadow);
    cursor: pointer;
    user-select: none;
}

.link:hover {
    color: #f9bd06;
    text-shadow: var(--primary-text-shadow);
}

.link::after {
    content: "";
    position: absolute;
    inset: 0;
    z-index: -1;
    border-left: 2px solid rgb(214, 33, 109);
    background-color: var(--background-color);
    transform: skew(45deg);
}

.link:hover::after {
	background-color: var(--background-color-hover);
}

.icon {
    display: none;
}




.dark {
    display: block;
    position: relative;
    padding: 10px 20px;
    text-transform: uppercase;
    text-decoration: none;
    text-shadow: var(--dark-primary-text-shadow);
    color: var(--dark-primary-color);
    cursor: pointer;
    user-select: none;
}

.dark:hover {
    color: var(--dark-primary-color-hover);
    text-shadow: var(--dark-primary-text-shadow);
}

.dark::after {
    content: "";
    position: absolute;
    inset: 0;
    z-index: -1;
    border-left: 2px solid rgb(214, 33, 109);
    background-color: var(--dark-background-color);
    transform: skew(45deg);
}

.dark:hover::after {
	background-color: var(--dark-background-color-hover);
    color: var( --dark-primary-color-hover);
}

@keyframes textrotate {
	0% {
		filter: drop-shadow(-2px 2px #6f5588);
	}

	25% {
		filter: drop-shadow(-2px -2px #6f5588);
	}

	50% {
		filter: drop-shadow(2px -2px #6f5588);
	}

	75% {
		filter: drop-shadow(2px 2px #6f5588);
	}

	100% {
		filter: drop-shadow(-2px 2px #6f5588);
	}
}

@media screen and (max-width: 1024px) {

    .link {
        padding: 8px 18px;
    }

    .link span {
        font-size: 0.8rem;
    }
}





@media screen and (max-width: 768px) {

    .nav {
        left: 0;
        margin-bottom: 0;
        border-bottom: 3px solid #fff;
        background-color: var(--background-color);
    }

    .list {
        justify-content: space-evenly;
        width: 100%;
    }

    .link span,
    .dark span {
        display: none;
    }

    .link::after,
    .dark::after {
        display: none;
    }

    

    
}