.item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 90px;
    padding: 10px;
    width: calc(100% / 3);
}

.item:nth-child(4n) {
    margin-right: 0;
}

.image {
    display: block;
    max-height: 110px;
    cursor: pointer;
}

.inner {
    text-align: center;
}

.title {
    margin: 10px 0;
    color: var(--primary-color);
    text-shadow: var(--primary-text-shadow);
}

.darkTitle {
    margin: 10px 0;
    color: var(--dark-primary-color);
    text-shadow: var(--dark-background-text-shadow);
}

.description {
    line-height: 2;
    letter-spacing: 0px;
    font-size: 0.7rem;
    color: var(--secondary-color);
    text-shadow: var(--secondary-text-shadow);
}

.darkDescription {
    line-height: 2;
    letter-spacing: 0px;
    font-size: 0.7rem;
    color: var(--dark-secondary-color);
    text-shadow: var(--dark-secondary-text-shadow);
}

@media screen and (max-width: 1024px) {
    .item {
        width: calc(100% / 2);
    }
}

@media screen and (max-width: 768px) {
    .item {
        flex-direction: row;
        width: 100%;
        margin-bottom: 40px;
    }

    .inner {
        text-align: left;
    }
}