.window {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 20%;
    left: calc(50% - 300px);
    z-index: 3;
    width: 600px;
    padding: 40px;
    border: var(--popup-border);
    border-radius: var(--popup-border-radius);
    background-color: var( --popup-background-color);
    text-shadow: var(--primary-text-shadow);
    color: var(--primary-color);
    user-select: none;
}

.darkWindow {
    background-image: var(--dark-popup-background-color);
    text-shadow: var(--dark-primary-text-shadow);
    color: var(--dark-primary-color);
}

.close {
    position: absolute;
    top: 3%;
    right: 3%;
    padding: 5px;
    border: none;
    background: none;
    color: #fff;
    cursor: pointer;
}

.title {
    margin-bottom: 40px;
    text-align: center;
}

.option {
    display: flex;
    justify-content: space-between;
    margin-bottom: 15px;
}

.button {
    border: none;
    font-size: 1rem;
    background-color: transparent;
    text-shadow: var(--primary-text-shadow);
    color: var(--primary-color);
    cursor: pointer;
}

.buttonActive {
    color: var(--primary-color-hover);
}

.darkButton {
    border: none;
    font-size: 1rem;
    background-color: transparent;
    text-shadow: var(--dark-primary-text-shadow);
    color: var(--dark-primary-color);
    cursor: pointer;
}

.darkButtonActive {
    text-shadow: var(--dark-background-text-shadow);
}

@media screen and (max-width: 768px) {
    .window {
        top: 0;
        left: 0;
        z-index: 5;
        width: 100%;
        height: 100%;
        border: none;
        border-radius: 0;       
    }

    .darkWindow {
        background-image: var( --dark-mobile-popup-background-color);
    }

    .option {
        align-items: center;
        margin-bottom: 50px;
    }

    .optionTitle {
        text-align: left;
        font-size: 1.5rem;
        line-height: 2;
    }

    .buttons {
        display: flex;
        flex-direction: column;
    }
    
    .buttons > .button:first-child,
    .buttons > .darkButton:first-child {
        margin-bottom: 20px;
    }

    .button,
    .darkButton {
        font-size: 1.5rem;
    }
}