:root {
    --background-color: #000;
    --background-color-hover: #ff0000;
    --primary-color: #b4ecff;
    --primary-text-shadow: 2px 2px #6f5588;
    --primary-color-hover: #f9bd06;
    --secondary-color: #feaa6f;
    --secondary-text-shadow: 2px 2px #974040;
    --popup-background-color: rgb(0 0 0 / 100%);
    --popup-border: 3px solid #fff;
    --popup-border-radius: 30px;

    --dark-background-color: #66bbff;
    --dark-background-color-hover: #fff;
    --dark-primary-color: #fff;
    --dark-primary-color-hover: #66bbff;
    --dark-primary-text-shadow: 2px 2px #000000;
    --dark-primary-text-shadow-hover: 2px 2px #fff;

    --dark-background-text-shadow: 2px 2px #ff009d;

    --dark-secondary-color: #7cb0f3;
    --dark-secondary-text-shadow: 2px 2px #733381;

    /* --dark-popup-background-color: rgb(102 187 255 / 100%); */
    --dark-popup-background-color: linear-gradient(to bottom, #3769a8, #3566a3, #33629e, #325f99, #305c94, #2e5a91, #2d578d, #2b558a, #295287, #275084, #254d81, #234b7e);
    --dark-mobile-popup-background-color: linear-gradient(to bottom, #3769a8, #33629e, #305c94, #2c558a, #294f80, #274b7a, #254875, #23446f, #21416c, #1f3f68, #1d3c65, #1b3a62);
   
}

::-webkit-scrollbar {
    width: 0;
  }

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
}

html,
body,
#root,
.App {
    height: 100%;
}

body {
    font-size: 16px;
    letter-spacing: 5px;
    font-family: "Quinquefive";
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center center;
}

button {
    font-family: "Quinquefive";
    letter-spacing: 5px;
}

a {
    color: inherit;
    text-decoration: none;
}

.App {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

@font-face {
    font-family: "Quinquefive";
    src: url(../font/Quinquefive.ttf);
}



