
.main {
    display: flex;
    flex: 1 0 100%;
    background-color: rgba(0, 0, 0, 0.8);
}

.container {
    max-width: 1366px;
    margin: 0 auto;
}

.content {
    display: flex;
    flex-direction: column;
}

@media screen and (max-width: 1024px) {
    .main {
        flex-direction: column;
    }
    
}

@media screen and (max-width: 768px) {
    .main {
        margin-top: 0;
    }
}