.section {
    display: none;
    padding: 0 40px;
}

.title {
    margin-bottom: 10px;
    color: var(--primary-color);
    text-shadow: var(--primary-text-shadow);
}

.current {
    border: none;
    font-size: 1rem;
    background: transparent;
    text-shadow: var(--primary-text-shadow);
    color: var(--primary-color-hover);
    cursor: pointer;
}

.list {
    /* position: absolute; */
    margin-top: 15px;
    padding: 20px 80px;
    border: var(--popup-border);
    background-color: var(--background-color);
}

.list > li {
    margin-bottom: 15px;
    font-size: 1rem;
    text-shadow: var(--primary-text-shadow);
    color: var(--primary-color-hover);
    cursor: pointer;
}

.dark {
    color: var(--dark-primary-color);
    text-shadow: var(--dark-background-text-shadow);
}

.dark > li {
    text-shadow: var(--dark-background-text-shadow);
    color: var(--dark-primary-color);
}

@media screen and (max-width: 768px) {
    .section {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 20px 0;
        text-align: center;
    }
    
}