.window {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 20%;
    left: calc(50% - 400px);
    z-index: 3;
    width: 800px;
    padding: 40px;
    border: var(--popup-border);
    border-radius: var(--popup-border-radius);
    background-color: var( --popup-background-color);
    text-shadow: var(--primary-text-shadow);
    color: var(--primary-color);
    user-select: none;
}

.darkWindow {
    background-image: var( --dark-popup-background-color);
    text-shadow: var(--dark-primary-text-shadow);
    color: var(--dark-primary-color);
}

.close {
    position: absolute;
    top: 3%;
    right: 3%;
    padding: 5px;
    border: none;
    background: none;
    color: #fff;
    cursor: pointer;
}

.title {
    margin-bottom: 40px;
    text-align: center;
}

.shopList {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.col {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-bottom: 40px;
}

.col:last-child {
    margin-bottom: 0;
}

.link {
    padding: 10px;
    border-radius: 30px;
    transition: transform .1s linear;
}

.link:hover {
    transform: translateY(-10px);
    background: var(--primary-color-hover); 
}

.darkLink:hover {
    transform: translateY(-10px);
    background: var(--dark-primary-color);
}

.logo {
    height: 100px;
}

.singleLink {
    font-size: 1.35rem;
    text-align: center;
}

.singleLink:hover {
    color: var(--primary-color-hover);
}

.darkSingleLink:hover {
    color: var(--dark-primary-color);
    text-shadow: var(--dark-background-text-shadow);
}

@media screen and (max-width: 768px) {
    .window {
        top: 0;
        left: 0;
        z-index: 5;
        width: 100%;
        height: 100%;
        border: none;
        border-radius: 0;
    }

    .darkWindow {
        background-image: var( --dark-mobile-popup-background-color);
    }

    .shopList {
        flex-direction: column;
    }

    .title {
        margin-bottom: 100px;
    }

    .col {
        flex-direction: column;
        align-items: center;
    }

    .link {
        margin-bottom: 40px;
        box-shadow: 0px 5px 4px #f9bd06;
    }

    .link:hover {
        transform: translateY(0);
    }

    .darkLink {
        box-shadow: 0px 5px 4px #fff;
    }
    
    .logo {
        height: 150px;
    }

    .singleLink {
        line-height: 2;
    }

}
