.sidebar {
    position: relative;
}

.list {
    position: sticky;
    top: 0;
    max-width: 300px;
    text-align: center;
}

.link {
    display: block;
    position: relative;
    z-index: 1;
    padding: 10px 20px;
    letter-spacing: 5px;
    font-size: 22px;
    word-wrap: break-word;
    text-transform: uppercase;
    color: var(--primary-color);
    text-shadow: var(--primary-text-shadow);
}

.link:hover {
    color: var(--primary-color-hover);
}

.active {
    color: var(--primary-color-hover);
    text-shadow: var(--primary-text-shadow);
}

.dark {
    display: block;
    position: relative;
    z-index: 1;
    padding: 10px 20px;
    letter-spacing: 5px;
    font-size: 22px;
    word-wrap: break-word;
    text-transform: uppercase;
    text-shadow: var(--dark-background-text-shadow);
    color: var(--dark-primary-color);
}

.dark:hover {
    color: var(--dark-primary-color-hover);
}

.darkActive {
    color: var(--dark-primary-color-hover);
}

@media screen and (max-width: 1024px) {

    .sidebar {
        position: sticky;
        top: 0;
        background-color: var(--background-color);
    }

    .list {
        display: flex;
        max-width: 100%;
        
    }

    .list li {
        width: calc(100% / 3);
    }

    .link,
    .dark {
        font-size: .9rem;
    }

    
    
}

@media screen and (max-width: 768px) {

    .list {
        display: none;
    }

}