.switcher {
    position: relative;
    left: 25px;
    display: flex;
    align-items: center;
    width: 100%;
    margin-bottom: 40px;
}

.item {
    position: relative;
    z-index: 2;
    width: 50%;
    padding: 16px 0;
    text-align: center;
    border: none;
    text-transform: uppercase;
    font-size: .8rem;
    background: transparent;
    text-shadow: var(--primary-text-shadow);
    color: #77dcff;
    cursor: pointer;
    user-select: none;
}

.item::after {
    content: "";
    position: absolute;
    inset: 0;
    z-index: -1;
    transform: skew(45deg);
    background-color: var(--background-color);
}

.active {
    text-shadow: var(--primary-text-shadow);
    color: var(--primary-color-hover);
}


.dark {
    position: relative;
    z-index: 2;
    width: 50%;
    padding: 16px 0;
    text-align: center;
    border: none;
    background: transparent;
    text-transform: uppercase;
    font-size: 0.8rem;
    text-shadow: var(--dark-primary-text-shadow);
    color: var(--dark-primary-color);
    cursor: pointer;
    user-select: none;
}

.dark::after {
    content: "";
    position: absolute;
    inset: 0;
    z-index: -1;
    transform: skew(45deg);
    background-color: var(--dark-background-color);
}

.darkActive {
    text-shadow: var(--dark-primary-text-shadow);
    color: var(--dark-primary-color-hover);
}

.darkActive.dark::after {
    background-color: var(--dark-background-color-hover);
}

@media screen and (max-width: 1024px) {
    .switcher {
        left: 0;
    }

    .item::after,
    .dark::after {
        transform: skew(0);
    }
    
} 


@media screen and (max-width: 768px) {
    .switcher {
        margin-bottom: 0;
    }

}