svg {
    display: none;
    width: 45px;
    height: 45px;
    filter: drop-shadow(var(--primary-text-shadow));
    animation: textrotate 1s linear infinite;
}

path {
    fill: var(--primary-color);
    
}

.dark {
    filter: drop-shadow(var(--dark-secondary-text-shadow));
}

.dark path {
    fill: var(--dark-primary-color);
}


@media screen and (max-width: 768px) {
    svg {
        display: block;
    }
}