.footer {
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 200px;
    border-top: 3px solid #fff;
    background-color: #000;
}

.textSection {
    margin-top: 60px;
    margin-left: 60px;
	font-size: 1rem;
    color: #77dcff;
    text-shadow: 2px 2px #6f5588;
    animation: textrotate 1s linear infinite;
    
}

.headSection {
	display: block;
    margin-top: -165px;
    margin-right: 100px;
    margin-left: 215px;
}

.headImage {
	display: block;
    width: 250px;
    animation: headrshaking 5s linear infinite;
    cursor: pointer;
}

@keyframes headrshaking {
	0% {
		transform: rotate(0);
	}

	25% {
		transform: rotate(7deg);
	}

    50% {
		transform: rotate(0);
	}

	75% {
		transform: rotate(-7deg);
	}


	100% {
		transform: rotate(0);
	}
}

@keyframes textrotate {
	0% {
		text-shadow: -2px 2px #6f5588;
	}

	25% {
		text-shadow: -2px -2px #6f5588;
	}

	50% {
		text-shadow: 2px -2px #6f5588;
	}

	75% {
		text-shadow: 2px 2px #6f5588;
	}

	100% {
		text-shadow: -2px 2px #6f5588;
	}
}

@media screen and (max-width: 768px) {

	.headSection {
		position: absolute;
    	right: 20px;
    	bottom: 200px;
		margin: 0;
		
	}

	.headImage {
		width: 200px;
	}

    .textSection {
		width: 100%;
		margin-top: 20px;
		margin-left: 0;
		padding: 0 20px;
		line-height: 2;
		font-size: .5rem;
	}
}