.window {
    display: flex;
    position: fixed;
    z-index: 3;
    left: calc(50% - 400px);
    width: 800px;
    padding: 60px;
    text-align: center;
    background-color: var(--background-color);
    border: var(--popup-border);
    border-radius: var(--popup-border-radius);
    transition: all .3s cubic-bezier(0.18, 0.89, 0.32, 1.28);
}

.darkWindow {
    background-image: var(--dark-popup-background-color);
}

.close {
    position: absolute;
    top: 3%;
    right: 3%;
    padding: 5px;
    border: none;
    background: none;
    color: #fff;
    cursor: pointer;
}

.button {
    padding: 10px;
    font-size: 1rem;
    border: none;
    background: none;
    color: #fff;
    cursor: pointer;
}

.cover {
    width: 200px;
    margin: 0 auto;
}

.image {
    display: block;
    width: 100%;
}

.title {
    text-shadow: var(--primary-text-shadow);
    color: var(--primary-color);
}

.darkTitle {
    text-shadow: var(--dark-primary-text-shadow);
    color: var(--dark-primary-color);
}

.description {
    line-height: 2;
    letter-spacing: 0;
    text-shadow: var(--secondary-text-shadow);
    color: var(--secondary-color);
}

.darkDescription {
    line-height: 2;
    letter-spacing: 0;
    text-shadow: var(--dark-background-text-shadow);
    color: var(--dark-primary-color);
}

@media screen and (max-width: 768px) {
    .window {
        left: 0;
        bottom: 0;
        width: 100%;
        padding-left: 0;
        padding-right: 0;
        border-radius: 0;
        border-left: 0;
        border-right: 0;
        border-bottom: 0;

    }

    .cover {
        max-width: 150px;
    }

    .description,
    .darkDescription {
        font-size: .75rem;
    }
}